<template>
    <h1>Twitter</h1>
    <input type="text" placeholder="Message:" v-model="mes" required/>
    <br />
    <br />
    <br />
    <br />
    <button v-on:click="getTwitter">Confirm</button>
</template>

<script>    
export default {
    name: "TwitterREA",
    data() {
        return {
            mes: null,
            tmpREA: []
        }
    },
    methods: {
        getTwitter() {
            if (this.mes != null) {
                let R = {
                    "message": this.mes
                }
                let REA = {
                    "content": JSON.stringify(R),
                    "id": "6366dc1caf9ba318ee9d8118",
                    "name": "Twitter",
                    "description": "Send a tweet"
                }
                if (localStorage.reaction) {
                    this.tmpREA = JSON.parse(localStorage.reaction)
                }
                this.tmpREA.push(REA)
                localStorage.reaction = JSON.stringify(this.tmpREA)
                this.$router.push({ name: 'Recap' });
            } else {
                alert('Message required')
            }
        },
    }
}
</script>

